<template>
    <div class="row">
    <!--right-->
    <div data-app class="col-12">                   
            <div class="form-group">
            <!--live search-->           
                <div style="display:flex;flex-direction:row;padding-left:0" class="col-12">
                    <div class="col-6 px0">
                        <div class="col-12 flex px0">
                            <div class="col-6 pl0">
                                <label>Select Department</label>
                                <select v-model="dept" class="form-control select" v-if="dept_ready" @change="new_list">
                                    <option v-for="dep in dept_list" :value="dep.deptmentid">{{ dep.department }}</option>
                                </select>
                            </div>
                            <!--
                            <div class="col-3">
                                <label>Type</label>
                                <select v-model="ftl" class="form-control select" v-if="dept_ready" @change="new_list">
                                    <option v-for="ft in fund_type ">{{ ft }}</option>
                                </select>
                            </div>
                            -->
                        </div>
                         <div class="col-12 flex px0">
                            <div class="col-6 flex pl0">
                                <div class="col-6 pl0">
                                    <button type="button" class="form-control" @click="newplan" style="margin-top:0.35rem">New</button>
                                </div>
                                <form method="post" action="src/Procurement/budget/backend/export.php" class="col-6 pr0">
                                    <input type="hidden" name="export" value='n'/>
                                    <button type="submit" class="form-control" style="margin-top:0.35rem">Export</button>   
                                </form>  
                            </div>
                        </div>                                                          
                    </div>
                    <div style="margin-left:auto;" class="col-4 pr0">
                        <div class="col-12">
                            <label>Filter(s)</label>
                        </div>
                        <div class="col-12">
                            <input type='text' class="form-control" placeholder="Search Record" v-model="key" @keyup="search_list">
                        </div>
                        <div class="col-12 flex px0" style='padding-top:0.35rem'>
                            <div class="col-6">
                                <input type="number" id='st_filter' class="form-control" v-model="st" @change="update_period"/>
                            </div>
                            <div class="col-6">
                                <input type="number" id='ed_filter' class="form-control" v-model="ed" readonly/>
                            </div>                           
                        </div>  
                    </div>
                </div>
            </div>       
            <input type='hidden' id='edit_budget' @click="edit_form"/>
            <div data-app class="col-12" style="padding-left:0;padding-right:0">                   
                    <div v-html="printlist">
                        {{ printlist }}
                    </div>
            </div>
            <!--list-->
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            dept_list:[],
            dept:'',
            dept_ready:false,
            assetlist:[],
            st:'',
            ed:'',
            insert_fund_name:'',
            fund_current : 'Regular',
            fund_type:['Regular','Special'],
            printlist:'',
            key:'',
            user_check:'',
            ftl:'Regular'
        }

    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
            .then(
                (response)=>{
                    this.user_check = response.data[0].signed;
                    var perm = response.data[0];
                    if(!(this.user_check=='set')){
                        this.$router.push('../user_setup');
                    }
                    if(perm.proc_level<3){
                        this.$alert("No permission").then(
                            ()=>{
                                this.$router.go(-1);
                            }
                        )
                    }
                }
            )
    await axios.get('Procurement/budget/backend/dept_member_high.php')
        .then(
            (response)=>{
                this.dept_list = response.data;                
                var prev = document.getElementById('query').value;
                if((prev!='')&&(prev!=null)&&(prev!=undefined)){
                    this.dept = prev;
                } else {
                    this.dept = this.dept_list[0].deptmentid;
                }
                this.dept_ready = true;
            }
        );   
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )      
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('type',this.ftl);
    await axios.post('Procurement/budget/backend/print_list_edit.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
            }
        )    
},
methods:{
    async update_period(){
        this.ed = parseInt(this.st)+1;
        this.$forceUpdate();
        await this.search_list();
    },
    search_list(event){
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('key',this.key);
    formData.append('type',this.ftl);
    axios.post('Procurement/budget/backend/print_list_edit_search.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
            }
        )
    },
    new_list(event){
    document.getElementById('query').value = this.dept;
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('type',this.ftl);
    axios.post('Procurement/budget/backend/print_list_edit.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
                this.key='';
            }
        )  
    },
    newplan(){
        document.getElementById('query').value=this.dept;
        this.$router.push('create_bp');
    },
    export_dept(){
        var formData = new FormData();
        formData.append('export','export');
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/budget/backend/export.php',formData);

    },
    choose_import_file(){
        this.$refs.import_file.click();
    },
    submit_import(){  
        var formData = new FormData();      
        formData.append('file',this.$refs.import_file.files[0]);
        console.log(this.$refs.import_file.files[0]);
        axios.post('Procurement/budget/backend/import.php',formData, {
            headers:{
               'Content-Type': 'multipart/form-data',
            }
        });
        //this.$refs.submit_import_file.click();
    },
    edit_form(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('edit_bp_full');
    }

}
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction: row;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.pl0{
    padding-left:0;
}
.pr0{
    padding-right:0;
}
</style>


